import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '@/components/Home'
import test1 from '@/components/Home/test1'
import Article from '@/components/Article'
import Detail from '@/components/Detail'
import Test from '@/components/Test'
import Detaill from '@/components/Detaill'
Vue.use(Router)

//const Hello = r => require.ensure([], () => r(require('@/components/HelloWorld')), 'HelloWorld');
//const Home1 = r => require.ensure([], () => r(require('@/components/Home')), 'Home');

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/Home',
      name: 'Home',
      component: Home,
      children: [
        {
          path: '/',
          name: 'test1',
          component: test1
        }
      ]
    },
    {
      path: '/Test',
      name: 'Test',
      component: Test
    },
    {
      path: '/Article',
      name: 'Article',
      component: Article
    },
    {
      path: '/Detail',
      name: 'Detail',
      component: Detail
    },
    {
      path: '/Detaill',
        name: 'Detaill',
      component: Detaill
    }
  ]
})



