  /* 必需 */
  .fadeIn-transition {
    transition: all .5s ease;
    opacity: 1;
  }

  /* .expand-enter 定义进入的开始状态 */
  /* .expand-leave 定义离开的结束状态 */
  .fadeIn-enter, .fadeIn-leave {
    opacity: 0;
