<template>
  <div class="fillcontain">

    <div class="table_container">
      <el-table
        :data="tableData"
        highlight-current-row
        style="width: 100%">
        <el-table-column
          type="index"
          width="100">
        </el-table-column>
        <el-table-column
          property="registe_time"
          label="注册日期"
          width="220">
        </el-table-column>
        <el-table-column
          property="username"
          label="用户姓名"
          width="220">
        </el-table-column>
        <el-table-column
          property="city"
          label="注册地址">
        </el-table-column>
      </el-table>
      <div class="Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="20"
          layout="total, prev, pager, next"
          :total="count">
        </el-pagination>
      </div>
    </div>

      <div class="table_container">
      <el-table
        :data="myTable"
        style="width: 100%">
        <el-table-column
          type="index"
          width="100">
        </el-table-column>
        <el-table-column
          property="thumb"
          label="注册日期"
          width="220">
        </el-table-column>
        <el-table-column
          property="title"
          label="用户姓名"
          width="220">
        </el-table-column>
        <el-table-column
          property="city"
          label="注册地址">
        </el-table-column>
      </el-table>
      <div class="" style="text-align: left;margin-top: 10px;display:none;">
        <ul id="example-1">
          <li v-for="item in myTable">
            {{ item.title }}
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  //import { mapState,mapActions } from 'vuex';
  import {getUserList, getUserCount,adminList2} from '../api/getData.js'
  export default {
    data(){
      return {
        tableData: [],
        myTable: [],
        currentRow: null,
        offset: 0,
        limit: 20,
        count: 0,
        currentPage: 1,
      }
    },
    computed:{
    },
    created(){
      this.initData();
      this.getnews1();

    },
    methods: {
      async initData(){
        try{
          const countData = await getUserCount();
          if (countData.status == 1) {
            this.count = countData.count;
          }else{
            throw new Error('获取数据失败');
          }
          this.getUsers();
        }catch(err){
          console.log('获取数据失败', err);
        }
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.offset = (val - 1)*this.limit;
        this.getUsers()
      },
      async getnews1(){
        const url="http://ec2-54-175-105-205.compute-1.amazonaws.com:80/www/index.php?m=sms&f=sms&v=test";
        this.myTable = [];
        this.$axios.get(url,{}).then(res=>{
          res.data.forEach(item => {
            const Data2 = {};
            Data2.title = item.title;
            Data2.thumb = item.thumb;
            Data2.city = item.id;
            this.myTable.push(Data2);
            //console.log(this.myTable);
          })
        });
      },
      async getnews(){
        const url="http://ec2-54-175-105-205.compute-1.amazonaws.com:80/www/index.php?m=sms&f=sms&v=test";
        //const Users = await adminList2({m: 'sms', f: 'sms',v: 'test'});
        $.get(url,function(data) {
          this.myTable = new Observer({});
          data.forEach((item,index) => {
            const Data2 = {};
            Data2.title = item.title;
            Data2.thumb = '广州';
            Data2.city = '广州';
            console.log(Data2);
            //this.myTable[index].title = item.title;
            //this.myTable[index].thumb = 'img';
            //this.myTable[index].city = '广州';
            const $r = this.myTable.push(Data2);
            console.log(this.myTable);
           //this.$set(this.myTable,'{title: "众筹已死：硬件创业公司如何在中美两地失去了支持者", thumb: "广州", city: "广州"}');
          })
        })
      },
      async getUsers(){
        const Users = await getUserList({offset: this.offset, limit: this.limit});
        this.tableData = [];
        Users.forEach(item => {
          const tableData = {};
          tableData.username = item.username;
          tableData.registe_time = item.registe_time;
          tableData.city = '上海';
          //console.log(tableData);
          this.tableData.push(tableData);
        })
      }
    },
  }
</script>

<style lang="less">
  @import '../style/mixin';
  .table_container{
    padding: 20px;
  }
</style>



