  .hexagon {
    display: inline-block;
    position: relative;
    padding: 45px 0;
    margin-bottom: 20px;
  }
  .hexagon .inner {
    vertical-align: middle;
    display: table-cell;
    width: 150px;
    height: 87px;
    -moz-transform: rotate(-30deg);
    -ms-transform: rotate(-30deg);
    -o-transform: rotate(-30deg);
    -webkit-transform: rotate(-30deg);
    transform: rotate(-30deg);
    border-left: 2px solid #FEDCAA;
    border-right: 2px solid #FEDCAA;
  }
  .hexagon:before, .hexagon:after {
    content: '';
    position: absolute;
    top: 45px;
    left: 0;
    width: 150px;
    height: 87px;
    -moz-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    -o-transform: rotate(90deg);
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
    border-right: 2px solid #FEDCAA;
    border-left: 2px solid #FEDCAA;
  }
  .hexagon:after {
    -moz-transform: rotate(30deg);
    -ms-transform: rotate(30deg);
    -o-transform: rotate(30deg);
    -webkit-transform: rotate(30deg);
    transform: rotate(30deg);
  }
  .col-md-2 p {font-size: 14px;color:#fff;margin: 0 auto;width: 100px;text-align: center;transform: rotate(30deg);}
